@use "sass:math";

* {
	padding: 0px;
	margin: 0px;
	border: 0px;
}
*,
*:before,
*:after {
	box-sizing: border-box;
}
:focus,
:active {
	// outline: none;
}
a:focus,
a:active {
	// outline: none;
}
html,
body {
	height: 100%;
	min-width: $min-width + px;
}
body {
	color: $font-main-color;
	line-height: 1;
	font-family: $font-family;
	font-size: $font-size;
	//text-rendering: optimizeLegibility;
	-ms-text-size-adjust: 100%;
	-moz-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}
input,
button,
textarea {
	font-family: $font-family;
	font-size: inherit;
}
button {
	cursor: pointer;
	color: inherit;
	background-color: inherit;
}
a {
	color: inherit;
}
a:link,
a:visited {
	text-decoration: none;
}
a:hover {
	// transition: color 0.3s ease 0s;
	text-decoration: none;
}
ul li {
	list-style: none;
}
img {
	vertical-align: top;
}
h1,
h2,
h3,
h4,
h5,
h6 {
	font-weight: inherit;
	font-size: inherit;
}


// Ограничивающий контейнер
/*
(i) Стили будут применяться ко  всем классам содержащим *container-offset
Например header__container-offset, main__container-offset и т.д.
*/
@if ($responsive-type==1) {
	// Отзывчивая
	[class*="container-offset"] {
		@if ($max-width-container>0) {
			max-width: rem($max-width-container);
			box-sizing: content-box;
			margin: 0 auto;
		}
		@if ($padding-container>0) {
			padding: 0 rem(math.div($padding-container, 2));
		}
	}
} @else {
	// По брейк-поинтам
	[class*="container-offset"] {
		margin: 0 auto;
		@if ($max-width-container>0) {
			max-width: rem($max-width-container);
			box-sizing: content-box;
		} @else {
			@if ($padding-container>0) {
				padding: 0 rem(math.div($padding-container, 2));
			}
		}
		@media (max-width: $bp-pc) {
			max-width: rem(970);
		}
		@media (max-width: $bp-tablet) {
			max-width: rem(750);
		}
		@media (max-width: $bp-mobile) {
			max-width: none;
			@if ($padding-container>0 and $max-width-container>0) {
				padding: 0 rem(math.div($padding-container, 2));
			}
		}
	}
}