.hero {
    // model block
    max-width: 1600px;
    margin: 0 auto;
    padding-top: 117px;
    padding-bottom: 50px;
    position: relative;
    @include adaptiveValue('margin-bottom',268,20,1600,1000);

    // style
    background-image: url('../img/hero-wrapper-bg.png');
    background-size: 687px 654px;
    background-repeat: no-repeat;
    background-position: top 80px right 105px;

    @media (max-width:1400px) {
        background-position: top 80px right;
    }

    @media (max-width:1150px) {
        background-position: top 50% right;
        background-size: 550px 550px;
    }

    @media (max-width:850px) {
        padding-top: 40px;
    }

    @media (max-width:$bp-tablet) {
        background-image: none;
    }

    // .hero__decore

    &__decore {
        // model block
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        z-index: -1;

        .decore-hero-1, .decore-hero-2, .decore-hero-3{
            position: absolute;
        }
        .decore-hero-1{
            top: 133px;
            @include adaptiveValue('left',657,0,1600);
            width: 20px;
            height: 20px;
            background-image: url('../img/svg/decore-hero-plus.svg');
            background-size: cover;
            background-repeat: no-repeat;
        }
        .decore-hero-2{
            top: 407px;
            @include adaptiveValue('left',80,0,1600);
            width: 26px;
            height: 26px;
            background-image: url('../img/svg/decore-hero-circle.svg');
            background-size: cover;
            background-repeat: no-repeat;
        }
        .decore-hero-3{
            top: 695px;
            left: 732px;
            width: 20px;
            height: 20px;
            background-image: url('../img/svg/decore-hero-circle.svg');
            background-size: cover;
            background-repeat: no-repeat;
        }
        @media (max-width:850px) {
            display: none;
        }
    }

    // .hero__wrapper

    &__wrapper {
        // model block
        max-width: 470px;

        @media (max-width:$bp-tablet) {
            max-width: 70%;
            margin: 0 auto;
        }

        @media (max-width:550px) {
            max-width: 100%;
        }
    }

    // .hero__title

    &__title {
        // model block
        margin: 0;
        margin-bottom: 70px;

        // typographic
        font-family: 'Playfair Display';
        font-weight: 700;
        font-size: 64px;
        line-height: 80px;
        letter-spacing: 2px;
        color: $font-color-ebony;

        @media (max-width:550px) {
            font-size: 44px;
        }
    }

    // .hero__form

    &__form {
    }
}
.form-hero {

    // .form-hero__title

    &__title {
        // model block
        margin: 0;
        margin-bottom: 71px;
        position: relative;

        // typographic
        font-weight: 600;
        font-size: 22px;
        line-height: 27px;
        color: $font-color-ebony;

        &::after{
            // model block
            content: ' ';
            position: absolute;
            bottom: -22px;
            left: 0;
            width: 32px;
            height: 2px;
            background-color: $font-color-ebony;
        }
    }

    // .form-hero__inner

    &__inner {
        // model block
        margin-bottom: 50px;

        .form-hero__line{
            &:not(:last-child){
                margin-bottom: 35px;
            }
        }
    }

    // .form-hero__line

    &__line {
        // model block
        display: flex;
        @media (max-width:420px) {
            flex-direction: column;
        }
    }

    // .form-hero__field

    &__field {
        // model block
        flex: 1 1 auto;

        &:not(:last-child){
            margin-right: 29px;

            @media (max-width:420px) {
                margin-right: 0;
                margin-bottom: 10px;
            }
        }
    }

    // .form-hero__select

    &__select {
    }

    // .form-hero__btn

    &__btn {
        // model block
        min-width: 180px;
        min-height: 52px;
    }

    .choices {
        width: 100%;
    
        &__inner{
            // model block
            margin: 0 !important;
            padding-left: 0 !important;
            padding-top: 3px !important;
            min-height: 52px;
    
            // style
            border: 1px solid #AFB0B9;
            border-radius: 4px;
            background-color: $font-color-light;
        }
    
        &:focus{
            // style
            outline: 2px solid $font-color-poppy;
            outline-offset: 5px;
            border-radius: 2px;
        }
    }
    
    .choices__list--single .choices__placeholder{
        // model block
        opacity: 1;
    
        // typographic
        font-style: normal;
        font-weight: 400;
        font-size: 15px;
        line-height: 32px;
        color: #4A4C53;
    }
    
    .choices[data-type*=select-one] .choices__inner{
        // model block
        padding-bottom: 0;
    }
    
    .choices__list--single{
        // model block
        height: 100%;
        display: flex;
        align-items: center;
        padding-left: 30px;
    }
    
    .choices[data-type*=select-one]::after{
        // model block
        border: none;
        margin-top: 0 !important;
        width: 9.5px;
        height: 6px;
        right: 30px;
    
        // style
        background-image: url('../img/svg/angle-down.svg');
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
        transform: translateY(-50%);
    
        // animation
        transition: transform 0.3s ease-in-out;
    }
    
    .choices[data-type*=select-one].is-open::after{
        // animation
        transform: rotate(180deg);
        
    }
    
    .choices__list--dropdown .choices__item--selectable:after{
        // display: none;
    }
    
    .choices__list--dropdown{
        // style
        background-color: $font-color-heather;
    }
}
