.stories {
    // model block
    position: relative;
    max-width: 1600px;
    margin: 0 auto;
    padding-top: 24px;
    @include adaptiveValue('margin-bottom',272,20,1600,1000,1);

    // .stories__decore

    &__decore {

        .decore-stories-1, .decore-stories-2, .decore-stories-3{
            position: absolute;
        }

        .decore-stories-1{
            top: -35px;
            @include adaptiveValue('left',645,300,1600);
            width: 20px;
            height: 20px;
            background-image: url('../img/svg/decore-hero-plus-nodeg.svg');
            background-size: cover;
            background-repeat: no-repeat;
        }
        .decore-stories-2{
            top: 390px;
            @include adaptiveValue('left',143,300,1600);
            width: 20px;
            height: 20px;
            background-image: url('../img/svg/decore-hero-circle.svg');
            background-size: cover;
            background-repeat: no-repeat;
        }
        .decore-stories-3{
            top: 628px;
            @include adaptiveValue('left',979,300,1600);
            width: 20px;
            height: 20px;
            background-image: url('../img/svg/decore-hero-circle.svg');
            background-size: cover;
            background-repeat: no-repeat;
        }
        @media (max-width:850px) {
            display: none;
        }
    }

    // .stories__head

    &__head {
        // model block
        display: flex;
        justify-content: space-between;
        align-items: baseline;
        margin-bottom: 53px;

        @media (max-width:550px) {
            flex-direction: column;
            align-items: flex-start;
        }
    }

    // .stories__title

    &__title {
        // typographic
        letter-spacing: 1.6px;
    }

    // .stories__link

    &__link {
        // model block
        top: -2px;
    }

    // .stories__contant

    &__contant {
        // model block
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
        column-gap: 25px;
    }

    // .stories__item

    &__item {
    }

    .container-offset{
        // model block
        max-width: rem(1120);
    }
}
.item-stories {
    // model block
    display: flex;
    flex-direction: column;
    overflow: hidden;

    &:hover{
        .item-stories__picture{
            img{
                transform: scale(1.05);
            }
        }
    }


    // .item-stories__picture

    &__picture {
        // model block
        margin-bottom: 13px;
        overflow: hidden;

        // style
        border-top-left-radius: 8px;
        border-top-right-radius: 8px;

        img{
            // model block
            display: block;
            width: 100%;
            height: 231px;
            object-fit: cover;
            object-position: top center;

            // animation
            transition: transform 0.3s ease-in-out;
        }
    }

    // .item-stories__title

    &__title {
        // model block
        margin-bottom: 12px;

        // typographic
        font-weight: 700;
        font-size: 19px;
        line-height: 30px;
        color: $font-color-black;
    }

    // .item-stories__descr

    &__descr {
        // model block
        margin-bottom: 23px;
        flex: 1 1 auto;

        // typographic
        font-weight: 400;
        font-size: 15px;
        line-height: 26px;
        color: $color-topaz;
    }

    // .item-stories__link

    &__link {
        // typographic
        font-weight: 600;
        font-size: 15px;
        line-height: 28px;
        color: $font-color-poppy;

        &:focus{
            outline-color: $font-color-poppy;   
        }
    }
}
