.header {
    // model block
    padding-top: 44px;

    // .header__logo

    &__logo {
        svg{
            // model block
            width: 41px;
            height: 41px;
        }
    }

    // .header__wrapper

    &__wrapper {
        // model block
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    // .header__inner

    &__inner {
        // model block
        display: flex;
        align-items: center;

        @media (max-width: 850px) {
            // model block
            position: fixed;
            left: 0;
            top: 0;
            min-width: 300px;
            height: 100vh;
            display: block;
            z-index: 500;
            padding: 40px 40px 40px 10px;
            transform: translateX(-100%);
            overflow-y: auto;

            // style
            background-color: $font-color-light;

            // animation
            transition: transform 0.3s ease-in-out
        }

        &--active{
            transform: none;
        }
        @media (max-width:420px) {
            min-width: 150px;
        }
    }

    // .header__nav

    &__nav {
    }

    // .header__btns

    &__btns {
        @media (max-width: 850px) {
            display: flex;
            justify-content: center;
        }
        @media (max-width: 480px) {
            flex-direction: column;
        }
    }

    // .header__btn

    &__btn {
        // model block
        min-height: 44px;
        min-width: 132px;

        &:not(:last-child){
            margin-right: 15px;

            @media (max-width: 480px) {
                margin-right: 0px;
                margin-bottom: 10px;
            }
        }
    }
}