.logo {
    // model block
    width: 41px;
    height: 41px;

    // style
    border-radius: 30%;

    &:focus{
        outline: 1px solid $font-color-poppy !important;
        outline-offset: 2px;
    }
    svg{
        // model block
        width: 100%;
        height: 100%;
        display: block;
        object-fit: cover;
    }
}
.nav {

    // .nav__list

    &__list {
        // model block
        display: flex;

        @media (max-width: 850px) {
            flex-direction: column;
            align-items: center;
            margin-bottom: 50px;
        }
    }

    // .nav__item

    &__item {
        // model block
        margin-right: 35px;

        @media (max-width: 850px) {
            margin-right: 0;

            &:not(:last-child){
                margin-bottom: 20px;
            }
        }
    }

    // .nav__link

    &__link {
        // typographic
        font-weight: 400;
        display: inline-block;
        font-size: 16px;
        line-height: 19px;
        color: $font-color-heather;

        // style
        border-radius: 2px;

        &--current{
            // typographic
            font-weight: 500;
            color: $font-color-ebony;

            &:hover{
                transform: none !important;
            }
        }

        // animation
        transition: transform 0.3s ease-in-out;

        &:focus{
            outline: 1px solid $font-color-poppy;
            outline-offset: 2px;
        }
        &:hover{
            transform: translateY(-3px);
        }
    }
}
.btn {
    // typographic
    font-weight: 500;
    font-size: 15px;
    line-height: 30px;
    padding: 0px 10px;

    // style
    border: 1px solid transparent;
    border-radius: 8px;

    // animation
    transition: transform 0.3s ease-in-out;

    &:focus{
        outline: 1px solid $font-color-poppy;
        outline-offset: 2px;
    }
    &:hover{
        transform: translateY(-3px);
    }
}
.link{
    // model block
    display: inline-flex;
    justify-content: center;
    align-items: center;

    // typographic
    font-weight: 500;
    font-size: 15px;
    line-height: 30px;
    padding: 0px 10px;

    // style
    border: 1px solid transparent;
    border-radius: 8px;

    // animation
    transition: transform 0.3s ease-in-out;

    &:focus{
        outline: 1px solid $font-color-poppy;
        outline-offset: 2px;
    }
    &:hover{
        transform: translateY(-3px);
    }
}
.btn-stroke {
    // typographic
    color: $font-color-poppy;

    // style
    border-color: $font-color-poppy;
}
.btn-fill {
    // typographic
    color: $font-color-light;

    // style
    background-color: $font-color-poppy;
}
.link-fill{
    // typographic
    color: $font-color-light;

    // style
    background-color: $font-color-poppy;
}
.title{
    // model block
    margin: 0;

    // typographic
    font-family: 'Playfair Display';
    font-weight: 700;
    font-size: 36px;
    letter-spacing: 0.8px;
    line-height: 50px;
    color: $font-main-color;
}
.descr{
    // model block
    margin: 0;

    // typographic
    font-family: 'Mulish';
    font-weight: 300;
    font-size: 17px;
    line-height: 30px;
    color: $color-topaz;
}
.view-link{
    // model block
    padding-right: 15px;
    position: relative;

    // typographic
    font-weight: 600;
    font-size: 17px;
    letter-spacing: 0.8px;
    line-height: 20px;
    color: $font-color-poppy;
    mix-blend-mode: normal;

    // animation
    transition: transform 0.3s ease-in-out;

    &::after{
        // model block
        content: " ";
        position: absolute;
        top: 50%;
        right: -5px;
        width: 7px;
        height: 12px;
        background-image: url('../img/svg/link-arrow.svg');
        background-repeat: no-repeat;
        background-size: contain;
        transform: translate(-50%,-50%);
    }

    &:focus{
        outline: 1px solid $font-color-poppy;
        outline-offset: 2px;
        border-radius: 2px;
    }
    &:hover{
        transform: translateX(-3px);
    }
}
.burger {
    --burger-width: 30px;
    --burger-height: 30px;
    --burger-line-height: 2px;

    position: fixed;
    z-index: 500;
    top: 50px;
    right: 50px;
    border: none;
    padding: 0;
    width: var(--burger-width);
    height: var(--burger-height);
    color: #000;
    background-color: transparent;
    cursor: pointer;
    display: none;

    &::before,
    &::after {
        content: "";
        position: absolute;
        left: 0;
        width: 100%;
        height: var(--burger-line-height);
        background-color: currentColor;
        transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
    }

    &::before {
        top: 0;
    }

    &::after {
        top: calc(100% - var(--burger-line-height));
    }

    &__line {
        position: absolute;
        left: 0;
        top: 50%;
        width: 100%;
        height: var(--burger-line-height);
        background-color: currentColor;
        transform: translateY(-50%);
        transition: transform 0.3s ease-in-out;
    }

    &--active {
        &::before {
        top: 50%;
        transform: rotate(45deg);
        transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
    }

        &::after {
        top: 50%;
        transform: rotate(-45deg);
        transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
        }

        .burger__line {
        transform: scale(0);
        transition: transform 0.3s ease-in-out;
        }
    }

    @media (max-width: 850px) {
        display: block;
        position: fixed;
    }
}
.overlay{
    // model block
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 499;
    visibility: hidden;

    // style
    background-color: rgba($color: #000000, $alpha: 0.6);
    opacity: 0;

    // animation
    transition: opacity 0.3s ease-in-out;

    &--active{
        opacity: 1;
        visibility: visible;
    }
}
.dis-scroll{
    // model block
    position: relative;
    overflow: hidden;
    height: 100vh;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
}