@use "sass:math";
@import "./mixins/percent-px-rem-em";

// Настройки полотна
$min-width: 320; // Минимальная ширина страницы
$max-width: 1600; // Ширина полотна (макета)
$max-width-container: 1220; // Ширина ограничивающего контейнера (0 = нет ограничения)
$padding-container: 30; // (30 = по 15px слева и справа, 0 = нет отступа)

// Ширина срабатывания первого брейкпоинта
$width-container: $max-width-container + $padding-container;

// Шрифт по умолчанию 
$font-family: "Inter", san-serif;
$font-size: rem(16); // где 14(px) - размер шрифта по умолчанию из макета

// Цвета
$font-main-color: #1D1D1F; // Цвет шрифта по умолчанию
$font-color-light: #fff;
$font-color-black: #000;
$font-color-ebony: #202336;
$font-color-heather: #B8BECD;
$font-color-poppy: #FB8F1D;

$color-topaz: #7D7987;
$color-cornflower:  #F9F9FB;
$color-gray: #848484;
$color-silver: #C4C4C4;

// Брейк-поинты
$bp-pc: em($width-container); // ПК, ноутбуки, некоторые планшеты в горизонтальном положении
$bp-tablet: em(991.98); // Планшеты, некоторые телефоны в горизонтальном положении
$bp-mobile: em(767.98); // Телефоны L
$bp-mobile-small: em(479.98); // Телефоны S

// Тип адаптива:
// 1 = отзывчивость (у контейнера нет брейкпоинтов),
// 2 = по брейк-поинтам (контейнер меняет свою ширину по брейк-поинтам)
$responsive-type: 1;