.guides {
    // model block
    max-width: 1600px;
    padding-top: 27px;
    margin: 0 auto;
    position: relative;
    @include adaptiveValue('margin-bottom',217,20,1600,1000);

    // style
    background-image: url('../img/guides-wrapper-bg.png');
    background-size: 599px 542px;
    background-repeat: no-repeat;
    background-position: top 0px right 230px;

    @media (max-width:1400px) {
        background-position: top right;
    }

    @media (max-width:$bp-tablet) {
        background-image: none;
    }

    // .guides__decore

    &__decore {
        // model block
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        z-index: -1;

        .decore-guides-1, .decore-guides-2, .decore-guides-3{
            position: absolute;
        }

        .decore-guides-1 {
            top: 40px;
            @include adaptiveValue('left',1428,100,1600);
            width: 16px;
            height: 16px;
            background-image: url('../img/svg/decore-hero-plus.svg');
            background-size: cover;
            background-repeat: no-repeat;
            transform: rotate(8deg);
        }
        .decore-guides-2 {
            top: 423px;
            @include adaptiveValue('left',659,100,1600);
            width: 20px;
            height: 20px;
            background-image: url('../img/svg/decore-hero-plus.svg');
            background-size: cover;
            background-repeat: no-repeat;
            transform: rotate(20deg);
        }
        .decore-guides-3 {
            top: 88px;
            left: 285px;
            @include adaptiveValue('left',285,100,1600);
            width: 20px;
            height: 20px;
            background-image: url('../img/svg/decore-hero-circle.svg');
            background-size: cover;
            background-repeat: no-repeat;
        }
        @media (max-width:850px) {
            display: none;
        }
    }

    // .guides__container

    &__container {
        // model block
        min-height: 550px;
        display: flex;
        justify-content: flex-start;
        min-height: 550px;

        @media (max-width:$bp-tablet) {
            justify-content: center;
        }
    }

    // .guides__contant

    &__contant {
        // model block
        max-width: 430px;
        align-self: center;

        @media (max-width:$bp-tablet) {
            max-width: 70%;
        }

        @media (max-width:550px) {
            max-width: 100%;
        }
    }

    // .guides__title

    &__title {
        // model block
        margin-bottom: 20px;
    }

    // .guides__descr

    &__descr {
        // model block
        margin-bottom: 39px;
    }

    // .guides__btn

    &__btn {
        // model block
        min-width: 164px;
        min-height: 48px;
    }

    .container-offset{
        // model block
        max-width: rem(1120);
    }
}