.explorer {
    // model block
    max-width: 1600px;
    margin: 0 auto;
    position: relative;
    @include adaptiveValue('margin-bottom',268,20,1600,900,2);

    // style
    background-image: url('../img/explorer-wrapper-bg.png');
    background-size: 659px 534px;
    background-repeat: no-repeat;
    background-position: top 15px left 138px;

    @media (max-width:1400px) {
        background-position: center left;
    }

    @media (max-width:1150px) {
        background-position: center left;
        background-size: 520px 500px;
    }

    @media (max-width:$bp-tablet) {
        background-image: none;
    }

    // .explorer__decore

    &__decore {
        // model block
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        z-index: -1;

        .decore-explorer-1, .decore-explorer-2{
            position: absolute;
        }
        .decore-explorer-1{
            top: -0px;
            @include adaptiveValue('left',827,100,1600);
            width: 20px;
            height: 20px;
            background-image: url('../img/svg/decore-hero-plus.svg');
            background-size: cover;
            background-repeat: no-repeat;
            transform: rotate(8deg);
        }
        .decore-explorer-2{
            top: 375px;
            @include adaptiveValue('left',1228,100,1600);
            width: 20px;
            height: 20px;
            background-image: url('../img/svg/decore-hero-circle.svg');
            background-size: cover;
            background-repeat: no-repeat;
        }
        @media (max-width:850px) {
            display: none;
        }
    }

    // .explorer__container

    &__container {
        // model block
        display: flex;
        justify-content: flex-end;
        min-height: 550px;

        @media (max-width:$bp-tablet) {
            justify-content: center;
        }
    }

    // .explorer__containt

    &__contant {
        // model block
        margin-right: 60px;
        align-self: center;
        max-width: 450px;

        @media (max-width:1400px) {
            margin-right: 0;
        }

        @media (max-width:$bp-tablet) {
            max-width: 70%;
        }

        @media (max-width:550px) {
            max-width: 100%;
        }
    }

    // .explorer__title

    &__title {
        // model block
        margin-bottom: 18px;
    }

    // .explorer__descr

    &__descr {
        // model block
        margin-bottom: 38px;
    }

    // .explorer__link

    &__link {
        // model block
        min-width: 164px;
        min-height: 48px;
    }
}