.testimonials {
    // model block
    max-width: 1600px;
    margin: 0 auto;
    @include adaptiveValue('margin-bottom',245,20,1600,1000);

    // .testimonials__title

    &__title {
        // model block
        margin-bottom: 35px;

        // typographic
        letter-spacing: 1.8px;
    }

    .container-offset{
        // model block
        max-width: rem(1120);
    }
}
.testimonials-slider {
    // model block
    padding-top: 20px;
    padding-bottom: 20px;

    // .testimonials-slider__wrapper

    &__wrapper {
    }
}
.testimonials-item {
    // model block
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-height: 490px;

    // .testimonials-item__contant

    &__contant {
        // model block
        max-width: 450px;

        @media (max-width:1400px) {
            max-width: 650px;
        }
    }

    // .testimonials-item__stars

    &__stars {
        // model block
        margin-bottom: 30px;

        span{
            // model block
            margin: 0;
            display: inline-block;
            width: 25px;
            height: 24px;
            background-image: url('../img/svg/testimonials-item-stars.svg');
            background-size: cover;
            background-repeat: no-repeat;

            &:not(:last-child){
                margin-right: 12px;
            }
        }
    }

    // .testimonials-item__quote

    &__quote {
        // model block
        margin-bottom: 60px;

        // typographic
        font-family: 'Mulish';
        font-weight: 400;
        font-size: 24px;
        line-height: 36px;
        color: $font-color-black;

        @media (max-width:$bp-tablet) {
            font-size: 17px;
            line-height: 26px;
            margin-bottom: 30px;
        }
    }

    // .testimonials-item__info

    &__info {
        // model block
        position: relative;
        top: -10px;
    }

    // .testimonials-item__name

    &__name {
        // model block
        display: block;
        margin-bottom: 10px;

        // typographic
        font-weight: 700;
        font-size: 22px;
        line-height: 26px;
        color: $font-color-black;
    }

    // .testimonials-item__position

    &__position {
        // model block
        display: block;

        // typographic
        font-weight: 400;
        font-size: 18px;
        line-height: 20px;
        color: rgba(0, 0, 0, 0.85);
    }

    // .testimonials-item__picture

    &__picture {
        // model block
        margin-right: 102px;
        position: relative;

        img{
            // model block
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: center;
        }

        .testimonials-slider-btns{
            // model block
            position: absolute;
            right: -1px;
            bottom: -1px;
            width: 133px;
            height: 61px;

            // style
            background-color: $font-color-light;
            border-top-left-radius: 16px;

            .testimonials-slider-button-prev {
                &::after{
                    // model block
                    content: ' ';
                    display: block;
                    position: absolute;
                    width: 40px;
                    height: 40px;
                    top: 13px;
                    right: -33px;
                    
                    // typographic
                    font-family: none;

                    // style
                    background-image: url('../img/svg/testimonials-slider-button-prev.svg');
                    background-size: cover;
                }
            }
            .testimonials-slider-button-next {
                &::after{
                    // model block
                    content: ' ';
                    display: block;
                    position: absolute;
                    width: 40px;
                    height: 40px;
                    top: 13px;
                    right: -9px;
                    
                    // typographic
                    font-family: none;

                    // style
                    background-image: url('../img/svg/testimonials-slider-button-next.svg');
                    background-size: cover;
                }
            }
        }
        @media (max-width:1400px) {
            margin-right: 0px;
        }

        @media (max-width:$bp-tablet) {
            max-width: 70%;
            min-width: 220px;
        }
    }
    
    // .testimonials-item__decore

    &__decore {
        // model block
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;

        .decore-testimonials-item-1,.decore-testimonials-item-2{
            position: absolute;
        }

        .decore-testimonials-item-1{
            top: 100%;
            left: 0;
            width: 32px;
            height: 32px;
            background-image: url('../img/svg/decore-hero-plus-nodeg.svg');
            background-size: cover;
            background-repeat: no-repeat;
            transform: translate(-50%,-50%);
        }
        .decore-testimonials-item-2{
            top: 4px;
            left: 100%;
            width: 42px;
            height: 42px;
            background-image: url('../img/svg/decore-hero-circle.svg');
            background-size: cover;
            background-repeat: no-repeat;
            transform: translate(-50%,-50%);
            z-index: -1;
            @media (max-width:1400px) {
                display: none;
            }
        }
    }
    @media (max-width:$bp-tablet) {
        flex-direction: column;
    }
}