.footer {
    // model block
    max-width: 1600px;
    margin: 0 auto;
    padding-top: 80px;
    padding-bottom: 20px;

    // style
    background-color: $color-cornflower;

    // .footer__items

    &__items {
        // model block
        display: flex;
        justify-content: space-between;
        // margin-bottom: 100px;
        @include adaptiveValue('margin-bottom',100,40,1250,998,2);

        @media (max-width:$bp-tablet) {
            flex-direction: column;
        }
    }

    // .footer__item

    &__item {
        // model block
        display: flex;
        flex-direction: column;
    }

    // .footer__logo

    &__logo {
        // model block
        width: 44px;
        height: 44px;
        margin-bottom: 26px;
    }

    // .footer__info

    &__info {
        // model block
        max-width: 340px;
        margin-bottom: 58px;

        // typographic
        font-family: 'Mulish';
        font-weight: 300;
        font-size: 18px;
        line-height: 28px;
        color: $color-gray;

        @media (max-width:$bp-tablet) {
            max-width: 100%;
            margin-bottom: 18px;
        }
    }

    // .footer__right

    &__right {
        // typographic
        font-family: 'Mulish';
        font-weight: 300;
        font-size: 17px;
        color: $color-gray;
        
        @media (max-width:$bp-tablet) {
            text-align: center;
        }
    }

    // .footer__dest

    &__dest, &__shop, &__interests{
        // model block
        padding-top: 11px;

        li{
            // typographic
            font-family: 'Mulish';
            font-weight: 700;
            // font-size: 19px;
            @include adaptiveValue('font-size',19,15,992,768,1);
            color: $font-color-black;

            &:first-child{
                margin-bottom: 28px;
            }
            &:not(:first-child){
                &:not(:last-child){
                    margin-bottom: 19px;

                    @media (max-width:$bp-mobile) {
                        margin-right: 10px;
                    }
                }
            }

            a{
                // typographic
                font-weight: 300;
                font-size: 18px;
                @include adaptiveValue('font-size',18,14,992,768,1);
                color: $color-gray;

                &:focus{
                    outline: 1px solid $font-color-poppy;
                    outline-offset: 2px;
                    border-radius: 4px;
                }
            }
        }
        @media (max-width: $bp-tablet) {
            display: flex;
            justify-content: space-between;
            flex-wrap: wrap;
        }
    }

    // .footer__dest
    &__dest {
        // model block
        position: relative;
        right: -10px;

        @media (max-width:$bp-tablet) {
            position: static;
        }
    }
    // .footer__shop
    &__shop {
        // model block
        position: relative;
        right: -18px;

        @media (max-width:$bp-tablet) {
            position: static;
        }
    }
    // .footer__interests
    &__interests {
    }

    // .footer__social

    &__social {
    }

    @media (max-width:$bp-tablet) {
        padding-top: 20px;
    }
}
.social {

    // .social__list

    &__list {
        // model block
        display: flex;
        justify-content: center;
        position: relative;
        padding-top: 20px;
        left: -31px;

        &::before{
            // model block
            content: " ";
            position: absolute;
            width: 100%;
            top: -12px;
            left: 31px;
            height: 0.6px;
            background-color: $color-silver;

            @media (max-width:999px) {
                display: none;
            }
        }

        @media (max-width:$bp-tablet) {
            position: static;
            margin-bottom: 15px;
        }
    }

    // .social__item

    &__item {

        &:not(:last-child){
            margin-right: 35px;
        }
    }

    // .social__link

    &__link {

        svg{
            // style
            fill: $font-color-black;

            // animation
            transition: fill 0.3s ease-in-out;
        }

        &:focus{
            // style
            outline: 1px solid $font-color-poppy;
            outline-offset: 2px;
            border-radius: 4px;
        }

        &:hover{
            svg{
                // style1
                fill:$font-color-poppy;
            }
        }
    }
}
