.destinations {
    // model block
    position: relative;
    max-width: 1600px;
    margin: 0 auto;
    padding-top: 24px;
    @include adaptiveValue('margin-bottom',235,20,1600,1000);

    // .destinations__decore

    &__decore {
        // model block
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        z-index: -1;

        .decore-destinations-1,.decore-destinations-2,.decore-destinations-3{
            position: absolute;
        }
        
        .decore-destinations-1{
            top: -8px;
            @include adaptiveValue('left',1169,300,1600);
            width: 20px;
            height: 20px;
            background-image: url('../img/svg/decore-hero-circle.svg');
            background-size: cover;
            background-repeat: no-repeat;
        }
        .decore-destinations-2{
            top: 324px;
            @include adaptiveValue('left',122,1300,1600);
            width: 20px;
            height: 20px;
            background-image: url('../img/svg/decore-hero-plus.svg');
            background-size: cover;
            background-repeat: no-repeat;
            transform: rotate(8deg);
        }
        .decore-destinations-3{
            top: 505px;
            @include adaptiveValue('left',743,300,1600);
            width: 20px;
            height: 20px;
            background-image: url('../img/svg/decore-hero-circle.svg');
            background-size: cover;
            background-repeat: no-repeat;
        }

        @media (max-width:$bp-tablet) {
            display: none;
        }
        @media (max-width:850px) {
            display: none;
        }
    }

    // .destinations__header

    &__header {
        // model block
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 55px;

        @media (max-width:550px) {
            flex-direction: column;
            align-items: flex-start;
        }
    }

    // .destinations__title

    &__title {
        // typographic
        letter-spacing: 1.3px;
    }

    // .destinations__link

    &__link {
        // model block
        top: 4px;
    }


    .container-offset{
        // model block
        max-width: rem(1120);
    }
}
.destination {
    // model block
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 25px;

    // .destination__limk

    &__link {
        // model block
        display: block;
    }

    // .destination__item

    &__item {
        // model block
        position: relative;
        overflow: hidden;
        flex: 1 1 auto;
        max-width: 350px;

        // style
        border-radius: 8px;

        // animation
        transition: transform 0.3s ease-in-out;

        &:focus{
            outline: 1px solid $font-color-poppy;
            outline-offset: 2px;
        }
        &:hover{
            transform: translateY(-3px);

            .destination__place{
                background-color: $font-color-poppy;
            }
            .destination__city{
                color: $font-color-light;
            }
            .destination__country{
                color: $font-color-light;
            }
        }
    }

    // .destination__picture

    &__picture {
        // model block1
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    // .destination__place

    &__place {
        // model block
        position: absolute;
        min-width: 120px;
        left: 0;
        bottom: 0;
        background-color: $font-color-light;

        // style
        border-top-right-radius: 8px;

        // animation
        transition: background-color 0.3s ease-in-out;
    }

    // .destination__city

    &__city {
        // model block
        display: block;
        position: relative;
        top: 4px;
        left: 0;
        padding-top: 6px;

        // typographic
        font-weight: 600;
        font-size: 17px;
        line-height: 25px;
        color: #042521;
    }

    // .destination__country

    &__country {
        // model block
        display: block;
        position: relative;
        top: 4px;
        left: 0;

        // typographic
        font-weight: 600;
        font-size: 15px;
        line-height: 25px;
        color: #80918E;
    }
}
